<template>
  <div>
    <div class="date-mask date-mask_active" @click="selectDate"></div>
    <div class="dp">
      <div class="dp-sel">
        <span>{{y}}</span>
        <a href="javascript:;">{{selStr}}</a>
      </div>
      <div class="dp-main">
        <div class="dp-header">
          <a class="dp-h-1" @click="pickYear(-1)">«</a>
          <a class="dp-h-2" @click="pickMonth(-1)">‹</a>
          <span class="dp-ym">{{y}}年 {{m}}月</span>
          <!--<a class="dp-h-3" @click="pickMonth(1)">›</a>-->
          <!--<a class="dp-h-4" @click="pickYear(1)">»</a>-->
          <a class="dp-h-3" :class="{gray:isMonthGray}" @click="pickMonth(1)">›</a>
          <a class="dp-h-4" @click="pickYear(1)">»</a>
        </div>
        <table class="dp-table">
          <thead>
          <tr>
            <th v-for="day in days"><span>{{day}}</span></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="cell in data">
            <td v-for="(item,index) in cell"  :class="{'dp-last': (m!== item.month), 'dp-today': cur === item.data, 'dp-select': sel === item.data}">
              <span @click="pickConform(item,index)">{{ item.day }}</span>
            </td>
          </tr>
          </tbody>
        </table>
        <div class="dp-footer">
        <span class="btn btn-cancel" @click="selectDate">取消</span>
        <span class="btn btn-ok" @click="selectSure">确定</span>
      </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'VueCalendar',
    components: {},
    props: ['date', 'dateStatus'],
    data() {
      var days = ['一', '二', '三', '四', '五', '六', '日'];
      var d = '', sel = '',selStr = '';
      var len = ('' + this.date).length;
      if (!this.date || (len != 13 && len != 10)) {
        d = new Date()
      } else {
        d = len == 13 ? new Date(parseInt(this.date)) : new Date(this.date * 1000)
      }
      if (Object.prototype.toString.call(d) === "[object Date]") {
        if (isNaN(d.getTime())) {
          d = new Date()
        } else {
          let month = d.getMonth() + 1;
          let day = d.getDate();
          sel = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
          //sel = d.getFullYear() + '-' + ((d.getMonth() + 1)<10?'0'+(d.getMonth() + 1):d.getMonth() + 1) + '-' + d.getDate()<10?'0'+d.getDate():d.getDate();
          selStr = (d.getMonth() + 1) + '月' + d.getDate() + '日';
        }
      } else {
        d = new Date()
      }
      if (!this.date) {
        sel = '';
        selStr = '';
      }
      var curTime = new Date();
      var cur = "" + curTime.getFullYear() + '-' + (curTime.getMonth() + 1) + '-' + curTime.getDate();//当前日期
      var y = d.getFullYear();
      var m = d.getMonth() + 1;
      var data = this.getCalendar(d.getFullYear(), d.getMonth() + 1);
      return {
        days: ['一', '二', '三', '四', '五', '六', '日'],
        cur,
        sel,
        y,
        m,
        data,
        //show: false,
        selStr,
        isMonthGray:true
      }
    },
    created: function () {
      var self = this;
    },
    methods: {
      getParams: function () {
        var self = this;
        self.curstatus = self.$route.query.curstatus;
        self.cureval = self.$route.query.cureval;


      },
      selectSure: function(){
        this.$emit('dateshow', this.sel);
        this.$emit('update:date', this.sel)
      },
      selectDate: function(){
        this.$emit('dateshow', this.sel)
      },
      pickMonth(flag) {
        if (flag == -1) {
          this.data = (this.m === 1) ? this.getCalendar(parseInt(this.y--), this.m = 12) : this.getCalendar(parseInt(this.y), parseInt(this.m += flag));
        } else {
          this.data = (this.m === 12) ? this.getCalendar(parseInt(this.y++), this.m = 1) : this.getCalendar(parseInt(this.y), parseInt(this.m += flag));
        }
      },
      pickYear(flag) {
        this.data = this.getCalendar(parseInt(this.y += flag), parseInt(this.m));
      },
      pickConform(item,index) {
        if(!this.CompareDate(this.cur,item.data)){
          this.$toast.center('请选择正确学习日期');
          return false;
        }

        this.sel = item.data;
        this.selStr = item.month + '月' + item.day + '日' + ' 周' + this.days[index];

      },
      CompareDate(currentTime,time) {//判断日期前后
        //传入值，如果第一个值晚于或等于第二个值则返回true，否则返回false
        return ((new Date(currentTime.replace(/-/g, "\/"))) >= (new Date(time.replace(/-/g, "\/"))));
      },
      getCalendar: function (y, m) {
        y = parseInt(y);
        m = parseInt(m);
        var time = new Date(y, m - 1, 1);
        var lastDate, nextDate;
        var lastMonth = m - 1;
        var nextMonth = m + 1;
        if (m === 1) {
          lastDate = "" + (y - 1) + '-' + +12 + '-';
          nextDate = "" + y + '-' + 2 + '-';
          lastMonth = 12
        } else if (m == 12) {
          lastDate = "" + y + '-' + 11 + '-';
          nextDate = "" + (y + 1) + '-' + 1 + '-';
          nextMonth = 1
        } else {
          lastDate = "" + y + '-' + (m - 1) + '-';
          nextDate = "" + y + '-' + (m + 1) + '-';
        }
        var maxNumber = 35;
        var r1 = [], r2 = [], r3 = [];
        var lastFix = time.getDay() - 1;
        lastFix = lastFix < 0 ? lastFix + 7 : lastFix;
        var lastMaxDate = new Date(y, m - 1, 0).getDate(); //上个月份最大天数
        var maxDate = new Date(y, m, 0).getDate();//当前月份的
        var i, t;
        var nextFix = maxNumber - maxDate - lastFix;
        for (i = 0; i < lastFix; i++) {
          t = lastMaxDate - lastFix + i + 1; r1[i] = { month: lastMonth, day: t, data: lastDate + t }
        }
        for (i = 0; i < maxDate; i++) {
          t = i + 1; r2[i] = { month: m, day: t, data: "" + y + '-' + +m + '-' + t }
        }
        for (i = 0; i < nextFix; i++) {
          t = i + 1; r3[i] = { month: nextMonth, day: t, data: nextDate + t }
        }
        var result = r1.concat(r2, r3);
        var ar = [];
        for (i = 0; i < 5; i++) {
          ar.push(result.splice(0, 7))
        }
        return ar

      }

    }
  }
</script>


<style scoped>
  .date-mask {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    opacity: .7;
    display: none;
  }

  .date-mask_active {
    z-index: 99;
    display: block;
  }

  .dp {
    margin-top: 5px;
    /*width: 526px;*/
    width: 550px;
    height: 720px;
    /*height: 690px;*/
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    border: none;
    position: fixed;
    top: 50%;
    left: 50%;
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 200;
    background: #fff;
    font-size: 24px;
    color: #333;
  }
  .dp-main{
    padding:0 30px;
    box-sizing: border-box;
  }
  .dp-table {
    width: 100%;
    text-align: center;
    border-collapse: collapse;
  }

  .dp-table th {
    padding: 6px 0;
    border: 0;
    height:60px;
    line-height:60px;
    font-size:26px;
    color:#666;
  }

  .dp-table td {
    /*padding: 4px 0;*/
    height:60px;
    line-height: 60px;
    border: 0;
    font-size:24px;
    color:#333;
    vertical-align: middle;
  }

  .dp-table th span {
    display: block;
    font-weight: normal;
  }

  .dp-table td span {
    display: block;
    width: 60px;
    height: 60px;
    border: 1px solid transparent;
    margin: 0 auto;
    transition: background 0.3s ease;
    border-radius: 30px;
    padding: 0;
    box-sizing: border-box;
  }

  .dp-table td span:hover {
    background: #eaf8fe;
    cursor: pointer;
  }

  .dp-table .dp-last span {
    color: #ccc;
  }

  .dp-table .dp-today span {
    border-color: #D19421;
    font-weight: bold;
    color: #D19421;
  }

  .dp-table .dp-select span {
    background: #D19421;
    color: #fff;
    border: 1px solid transparent;
  }

  .dp-table .dp-select span:hover {
    background: #D19421;
  }

  .dp-header {
    position: relative;
    text-align: center;
    /*height: 60px;*/
    height: 90px;
    line-height: 90px;
    border-bottom: 1px solid #e9e9e9;
  }

  .dp-header .dp-ym {
    font-weight: bold;
    font-size:26px;
    color:#333;
  }

  .dp-header a {
    color: #999;
    line-height: 90px;
    height: 90px;
    font-size: 40px;
    display: inline-block;
    /*padding: 0 5px;*/
    position: absolute;
    /*position: relative;*/
    width:60px;
  }

  .dp-header a:hover {
    color: #D19421;
    cursor: pointer;
  }

  .dp-header .dp-h-1 {
    /*left: 7px;*/
    left: 0;
  }

  .dp-header .dp-h-2 {
    /*left: 29px;*/
    left: 60px;
  }

  .dp-header .dp-h-3 {
    /*right: 29px;*/
    right: 60px;
  }

  .dp-header .dp-h-4 {
    /*right: 7px;*/
    right: 0;
  }
  .dp-sel{
    width:100%;
    height:150px;
    background-color:#D19421;
    color:#fff;
    text-align: left;
    padding-left:30px;
  }
  .dp-sel span{
    display: block;
    font-size:28px;
    padding:30px 0 10px 0;

  }
  .dp-sel a{
    color:#fff;
    font-size:50px;
  }
  .dp-footer {
    height: 60px;
    text-align: center;
    margin: 40px 0 10px 0;
    float:right;
  }

  .dp-footer a {
    display: inline-block;
    line-height: 60px;
    height: 60px;
    cursor: pointer;
  }

  .dp-footer span {
    /*position: absolute;*/
    /*right: 16px;*/
    /*top: 7px;*/

  }
  .dp .btn {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    white-space: nowrap;
    line-height: 1.5;
    padding: 0 20px;
    font-size: 28px;
    color: #D19421;
  }
  .dp .btn-cancel{
    color:#666;
    margin-right:30px;
  }
  .dp-header .gray{
    color:#ccc;
  }
</style>



