<template>
  <div class="sf-wrap">
    <!--<p class="tip" @click="toRefund">领取失败，点击查看退款></p>-->
    <p v-if="curstatus== 2" class="tip">您已申领过该证书, 等待审核</p>
    <p v-else-if="curstatus== 3" class="tip">您已申领过该证书, 正在审核中</p>
    <!--<p v-else-if="curstatus== 4 && payStatus == 6" class="tip">领取失败, 请重新填写信息</p>-->
    <p v-else-if="curstatus== 4 && payStatus == 6" class="tip" @click="toRefund">领取失败, 点击查看退款进度 </p>
    <p v-else-if="curstatus== 4 && payStatus == 7" class="tip" @click="toRefund">领取失败, 点击查看退款进度 </p>
    <p v-else-if="curstatus== 4 && payStatus == 0" class="tip">领取失败, 请重新填写 </p><!--免费，已拒接-->
    <p v-else-if="curstatus== 6" class="tip" @click="toRefund">领取失败, 点击查看退款进度 ></p>
    <p v-else class="tip">请填写申请信息</p>
    <!--<p v-if="cureval=='d' || cureval == 'z'" class="tip">领取失败，点击查看详情></p>-->
    <div class="wrap">
      <div class="gc-img-cont">
        <img src="../assets/img/getcer-tit-img.png" alt="">
      </div>
      <div class="gc-main">
        <h4 class="gc-tit">中商会财税专委会证书领取</h4>
        <hr class="gc-tit-line"/>

        <div class="gc-form">
          <div class="gc-form-item">
            <h5 class="gc-form-tit">学习地区</h5>
            <a class="gc-form-inp chooseArea" @click="maskStatus">{{areaStatus}}</a>
            <area-select :data="areaList" :status="areaSelect" @areashow="maskStatus" :class="{'none':!areaSelect}" :area = 'areaStatus'  @update:area="val => areaStatus = val"></area-select>

          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">学习日期</h5>
            <a class="gc-form-inp chooseArea" @click="checkDate">{{dateStatus}}</a>
            <!--<vue-calendar :date="time" :dateStatus="dateSelect" @dateshow="checkDate" :class="{'none':!dateSelect}" @update:date="val => dateStatus = val"></vue-calendar>-->
            <vue-calendar :date="time" :dateStatus="dateSelect" @dateshow="checkDate" :class="{'none':!dateSelect}" @update:date="getDateStatus"></vue-calendar>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">姓名</h5>
            <input class="gc-form-inp" v-model="name" type="text" placeholder="请输入领证人的姓名"/>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">英文名</h5>
            <p class="gc-form-inp" :oldname="name" v-py.upperfirst="name" v-show="!showEnlishInp" @click="clickShowEnlishInp">{{english_name}}</p>
            <input class="gc-form-inp" v-if="showEnlishInp" v-model="english_name" type="text"/>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">手机号</h5>
            <input class="gc-form-inp" v-model="tel" type="number" placeholder="请输入领证人的手机号"/>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">身份证号</h5>
            <input class="gc-form-inp" v-model="card" type="text" placeholder="请输入领证人的身份证号"/>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">公司名称</h5>
            <input class="gc-form-inp" v-model="company" type="text" placeholder="请输入公司名称"/>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">公司性质</h5>
            <select class="gc-form-inp gc-form-select" v-model="nature">
              <option :value="index" v-for="item,index in property">{{item}}</option>
            </select>

          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">公司规模</h5>
            <select class="gc-form-inp gc-form-select" v-model="scale">
              <option :value="index" v-for="item,index in size">{{item}}</option>
            </select>
          </div>
          <div class="gc-form-item">
            <h5 class="gc-form-tit">职位</h5>
            <select class="gc-form-inp gc-form-select" v-model="job">
              <option :value="index" v-for="item,index in position">{{item}}</option>
            </select>
          </div>
          <div class="gc-btn-cont">
            <button class="gc-btn" :class="{dis:isDisabled}" id="saveBtn" @click="saveForm" :disabled="isDisabled">提交</button>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import Qs from 'qs'
  import VDistpicker from 'v-distpicker'
  import areaSelect from '../components/AreaSelect/AreaSelect'
  import vueCalendar from '../components/VueCalendar/VueCalendar'
  import JSON_ from '../components/AreaSelect/data.json'
  //import py from '../assets/js/vue-py'
  require('../assets/js/vue-py.js')

  export default {
    name: 'SetForm',
    components: {
      VDistpicker,
      areaSelect,
      vueCalendar
    },
    data() {
      return {
        showArea: false,
        AreaArr: [],

        name: '',
        english_name:'',
        tel:'' ,
        card: '',
        company: '',

        property: {
          "0": "外商独资/外企办事处",
          "1": "中外合营（合资/合作）",
          "2": "私营/民营企业",
          "3": "国有企业",
          "4": "国内上市公司",
          "5": "政府机关/非盈利机构",
          "6": "事业单位",
          "7": "其他",
        },
        size: {
          "1": "1-49人",
          "2": "50-99人",
          "3": "100-499人",
          "4": "500-999人",
          "5": "1000-2000人",
          "6": "2000-5000人",
          "7": "5000-10000人",
        },
        position: {
          "1": "经理",
          "2": "主管",
          "3": "财务主管/总账主管",
          "4": "审计专员/助理",
          "5": "会计/会计师",
        },
        nature: '0',
        scale: '1',
        job: '2',

        curstatus: '',
        curCertId: '',
        curCertName:'',

        areaList:[], // 地址数据
        areaSelect:false, // 控制地址选择器显示
        areaStatus: '请选择学习地区', // 更新选择之后的地址
        // areaStatus: '河北省>沧州市>河西区', // 更新选择之后的地址

        dateStatus:'请选择学习日期',
        // dateStatus:'2018-09-21',
        dateSelect:false,
        time: new Date().getTime(),

        showEnlishInp:false,
        isDisabled:false,
        payStatus:0
      }
    },
    created: function () {
      this.getParams();
      this.areaList = JSON_;//地址数据赋值
    },
    methods: {
      getParams: function () {
        var self = this;
        self.curstatus = self.$route.query.curstatus;
        self.curCertId = self.$route.query.curcertid;
        self.curCertName = self.$route.query.curcertname;
        console.log(self.curstatus);
        if(self.curstatus == 2 || self.curstatus == 3 || self.curstatus == 6){
          self.isDisabled = true;
        }else{
          self.isDisabled = false;
        }
        if(self.curstatus != 11){
          self.getCertStatusData();
        }

      },
      getCertStatusData(){
        let self = this;
        self.$axios({
          method:"get",
          url:self.baseUrl+'/api/v1/weixin/applicants/users/'+self.curCertId + '/' + localStorage.openid
        }).then((res)=>{
          if(res.data.success){
            let formData = res.data.data;
            self.areaStatus = formData.province + '>' + formData.city + '>' + formData.district;
            self.dateStatus = formData.study_date;
            self.name = formData.name;
            self.english_name = formData.english_name;
            self.tel = formData.phone;
            self.card = formData.personal_id;
            self.company = formData.company_name;
            self.nature = formData.company_nature;
            self.scale = formData.company_scale;
            self.job = formData.job;
            self.payStatus = formData.pay_status;
          }else{
            //self.$toast.center(res.data.msg);
          }
        })


      },
      getDateStatus(date){
        //console.log(date);
        var arr = date.split('-');
        var month =arr[1]<10?'0'+arr[1]:arr[1];
        var day = arr[2]<10?'0'+arr[2]:arr[2];
        this.dateStatus = arr[0] + '-' + month +'-'+ day;
        // this.dateStatus = date;
      },
      clickShowEnlishInp(e){
        var english_name = e.target.innerText;
        this.english_name = english_name;
        this.showEnlishInp = !this.showEnlishInp;
      },
      maskStatus: function () { // 更改显示状态
        this.areaSelect = !this.areaSelect;
      },
      checkDate: function(){
        this.dateSelect = !this.dateSelect;
      },
      choose() {
        this.showArea = !this.showArea
      },
      onChangeProvince(a) {
        this.AreaArr[0] = a.value;
      },
      onChangeCity(a) {
        this.AreaArr[1] = a.value;
      },
      checkForm: function () {
        var self = this;
        if (self.areaStatus.indexOf('>') < 0) {
          self.$toast.center('请选择学习地区');
          return false;
        }
        if(self.dateStatus == '请选择学习日期'){
          self.$toast.center('请选择学习日期');
          return false;
        }
        if (!self.name) {
          self.$toast.center('请输入姓名');
          return false;
        }
        if(escape(self.english_name).indexOf( "%u" )>-1){//escape对字符串进行编码时，字符值大于255的以"%u****"格式存储，而字符值大于255的恰好是非英文字符（一般是中文字符，非中文字符也可以当作中文字符考虑）
          self.$toast.center('英文名不能包含中文');
          return false;
        }
        var reg_tel = /^(0|86|17951)?\s*(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
        if (!self.tel) {
          self.$toast.center('请输入手机号');
          return false;
        } else if (!reg_tel.test(self.tel)) {
          self.$toast.center("请输入正确手机号！");
          return false;
        }
        var reg_card = /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|[xX])$/;
        if (!self.card) {
          self.$toast.center('请输入身份证号');
          return false;
        } else if (!reg_card.test(self.card)) {
          self.$toast.center("请输入正确身份证号！");
          return false;
        }
        if (!self.company) {
          self.$toast.center('请输入公司名称');
          return false;
        }

        return true;


      },

      saveForm: function () {
        var self = this;
        if(!self.checkForm()){
          self.$toast.center('输入有误，请检查');
          return false;
        }
        var data = {
          cert_id:self.curCertId,
          cert_name:self.curCertName,
          cert_type:0,

          name: self.name,
          english_name:self.english_name,
          personal_id: self.card,
          phone: Number(self.tel),

          address: '',
          province:self.areaStatus.split('>')[0],
          city:self.areaStatus.split('>')[1],
          district:self.areaStatus.split('>')[2],
          study_date:self.dateStatus,

          company_name: self.company,
          company_nature: self.nature,
          company_scale: self.scale,
          job:self.job,

          //wechat_id: Math.floor(Math.random()*999999999999999999)+'',
          wechat_id: localStorage.openid
        }
        self.$axios({
          method:"post",
          url:self.baseUrl+'/api/v1/weixin/applicants/users',
          headers:{
            'Content-Type': 'application/json'
          },
          data:data,
        }).then((res)=>{
          console.log(res);
          if(res.data.success){
            self.getOrderMsg();
          }else{
            self.$toast.center(res.data.msg);
          }
        })
      },
      getOrderMsg(){
        let self = this;
        let openid = localStorage.getItem('openid');
        self.$axios({
          method:"get",
          url:self.baseUrl+'/api/v1/weixin/wxorder/'+ openid +'/'+ self.curCertId,
          // url:self.baseUrl+'/api/v1/weixin/wxorder/'+ openid,
        }).then((res)=>{
          if(res.data.success){
            let data = res.data.data;
            let price = data.price;
            if(price == 0){//免费
              this.$router.push({path:'/Wait'});
            }else{
              let appid = data.appid;
              let prepayid = data.prepay_id;
              let name = data.name;
              let apikey = data.apikey;
              let orderid = data.orderid;

              this.$router.push({path:'/Order',query:{appid:appid,prepayid:prepayid,price:price,name:name,orderid:orderid,apikey:apikey}});
            }


          }else{
            self.$toast.center(res.data.msg);
          }
        })


      },
      toRefund(){

        this.$router.push({path:'/Refund',query:{openid:localStorage.openid,cert_id:this.curCertId,cert_name:this.curCertName,cert_status:this.curstatus}})
      },

    }

  }
</script>


