    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    outline: none;

  }

  .gc-form-select {
    background: url("../assets/img/down.png") no-repeat 620px center;
    background-size: 26px 15px;

  }

  .gc-btn-cont {
    width: 620px;
    margin: 104px auto 0;
  }

  .gc-btn {
    width: 620px;
    height: 78px;
    line-height: 78px;
    background-color: #d19421;
