    border: none;
    border-radius: 6px;
  }

  .select-xz {
    /*z-index: 4000;*/
  }

  .select-gm {
    /*z-index:3000*/
  }

  .select-zw {
    /*z-index: 2000;*/
  }

  .areacont {
    position: relative;
    width: 100%;
    height: 87px;
