    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    outline: none;
  }

  /*v-distpicker样式*/
  .divwrap {
    height: 400px;
    overflow-y: auto;
    position: fixed;
    left: 0;
